import React from 'react'
import styled, { keyframes } from 'styled-components'
import IconScroll from './common/IconScroll'
import Typewriter from 'typewriter-effect';
import nameAudio from '../assets/sounds/name.mp3';

export default function Welcome() {
    const bgScrollingReverse = keyframes`
    100% { background-position: 50px 50px; }
    `;
    const Container = styled.div`
        height: 100vh;
        width: 100vw;
        background: #000;
        color: #fff;
        display: flex;
        flex-direction: column;
        text-align: center;
        background: url('data:image/png;base64,') repeat 0 0;
        animation: ${bgScrollingReverse} 1.6s infinite;
        animation-timing-function: linear;
    `;
    const HelloText = styled.div`
        margin: auto;
        font-size: 42px;
        max-width: 95vw;
        line-height: 1.7;
        @media (max-width: 768px) {
            font-size: 30px;
        }
    `;
    const ScrollContainer = styled.div`
        margin: 0 auto;
        margin-bottom: 22px;
        font-size: 18px;
    `;
    const Name = styled.span`
        color: #ff7990;
        cursor: pointer;
    `;
    const audio = new Audio(nameAudio)

    return (
        <Container>
            <HelloText>
                <span style={{fontSize: "1.2em", fontWeight: "bold"}}>Hello, I'm <Name onClick={() => audio.play()}>Ashraf El Ghinoussi</Name></span><br />
                <Typewriter
                    options={{
                        autoStart: true,
                        loop: true,
                        delay: 75,
                    }}
                    onInit={(typewriter) => {
                        typewriter.typeString("I'm a Full-stack web developer")
                            .changeDeleteSpeed(30)
                            .pauseFor(2500)
                            .deleteChars(24)
                            .typeString("Designer")
                            .pauseFor(1700)
                            .deleteChars(8)
                            .typeString("Developer")
                            .pauseFor(1700)
                            .start()
                            ;
                    }}
                />
            </HelloText>
            <ScrollContainer>
                <IconScroll color="#fff" text="scroll &#10;&#13; if you dare" />
            </ScrollContainer>
        </Container>
    )
}
